html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
}

.box {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.box-sm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  margin: 0;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }
  body .tagline,
  body .acciones a {
    color: #FFFFFF;
  }
  body .descripcion-pagina,
  body .nombre-pagina,
  body .enlace {
    color: #efefef;
  }
  body .form .campo input,
  body .main .form .campo input {
    background-color: #6c7a8f;
    color: #FFFFFF;
  }
  body .form .campo input::placeholder {
    color: #cccccc;
  }
  body .form .campo .icon,
  body .main .form .campo .icon {
    background-color: #6c7a8f;
    color: #FFFFFF;
  }
  body .alert .err,
  body .alert .enviado {
    background-color: #FFFFFF;
  }
  body .dashboard {
    background-color: #4B5563;
  }
  body .barra {
    background-color: #3c444f;
  }
  body .barra p {
    color: #FFFFFF;
  }
  body .barra p span {
    color: #f8bb54;
  }
  body .listado-proyectos .proyecto {
    background-color: #616e80;
  }
  body .listado-proyectos .proyecto:hover {
    background-color: #586474;
  }
  body .listado-tareas .tarea {
    background-color: #616e80;
    border-radius: 1.5rem;
    border: none;
    transition: background-color 0.3s ease;
  }
  body .listado-tareas .tarea:hover {
    background-color: #5b6879;
  }
  body .listado-tareas .tarea p {
    color: #efefef;
  }
  body .listado-tareas .tarea .nombre-box .icon {
    color: #FFFFFF;
  }
  body .filtros {
    background-color: #616e80;
  }
  body .filtros .filtros-inputs h2, body .filtros .filtros-inputs label {
    color: #FFFFFF;
  }
  body .filtros .filtros-inputs input[type=radio] {
    accent-color: #4B5563;
  }
  body .sidebar {
    background-color: #353c46;
  }
  body .sidebar .sidebar-button {
    background-color: #1f2329;
  }
  body .sidebar .option {
    background-color: #F59E0B;
  }
  body .sidebar .option:hover {
    background-color: #f6a61f;
  }
  body .sidebar .option.active {
    background-color: #c57f08;
  }
  body .logout i {
    color: #f8bb54;
  }
  body .modal .form.nueva-tarea {
    background-color: #4B5563;
  }
  body .modal .form.nueva-tarea legend {
    color: #FFFFFF;
  }
  body .panel .main p {
    color: #FFFFFF;
  }
}
.form {
  margin-top: 5rem;
  max-width: 70rem;
  width: 100%;
}
.form .campo {
  margin-bottom: 2rem;
  overflow: hidden;
  border-radius: 1.5rem;
  display: grid;
  grid-template-columns: 15rem 1fr 5rem;
  width: 100%;
  min-height: 4rem;
}
.form .campo label {
  padding-left: 2rem;
  font-weight: bold;
  color: #FFFFFF;
  display: flex;
  align-items: center;
}
.form .campo input {
  padding-left: 1rem;
  border: none;
  background-color: #d8dadf;
  outline: none;
}
.form .campo .icon {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #d8dadf;
  cursor: pointer;
}
.form .boton {
  border-radius: 1rem;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    width: 50rem;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  font-weight: bold;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alert {
  max-width: 80rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.alert .err, .alert .enviado {
  padding: 0.7rem;
  font-size: 1.6rem;
  font-weight: bold;
  width: 100%;
  text-align: center;
}
.alert .err {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alert .enviado {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

