.justify-center {
  justify-content: center;
}

.col-span-2 {
  grid-column: span 2/span 2;
  background-color: red;
}

.login {
  padding-top: 6rem;
}
.login .uptask {
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}

.dashboard {
  background-color: #efefef;
  height: 100vh;
  display: flex;
}
.dashboard .panel {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: 7rem 1fr;
}

.sidebar {
  background-color: #FFFFFF;
  width: 10rem;
  transition: width 0.3s;
  transition: all 0.3s ease;
  position: relative;
  height: 100vh;
  padding: 7rem 2rem;
  position: absolute;
}
.sidebar.colapsed {
  width: 0rem;
  padding: 7rem 1rem;
}
.sidebar.colapsed .option-text {
  display: none;
}
@media (min-width: 768px) {
  .sidebar {
    position: relative;
    width: 10rem;
  }
  .sidebar.colapsed {
    width: 0rem;
    padding: 7rem 2rem;
  }
}
@media (min-width: 1024px) {
  .sidebar {
    width: 30rem;
  }
  .sidebar.colapsed {
    width: 10rem;
  }
}
.sidebar .sidebar-button {
  position: absolute;
  top: 2rem;
  right: -1.75rem;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #2563EB;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  cursor: pointer;
  color: #FFFFFF;
}
.sidebar .sidebar-button i {
  transform: rotate(90deg);
  transition: transform 0.3s ease;
}
.sidebar .sidebar-button .hidden {
  display: none;
}

.sidebar-nav {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.sidebar-nav .option {
  display: grid;
  grid-template-columns: 6rem 26rem;
  margin-bottom: 2rem;
  align-items: center;
  height: 6rem;
  border-radius: 1rem;
  transition: background-color 0.3s ease;
  background-color: #2563EB;
  overflow: hidden;
  cursor: pointer;
}
.sidebar-nav .option.active {
  background-color: #7C3AED;
}
.sidebar-nav .option:hover {
  background-color: #5f14e0;
}
.sidebar-nav .option .option-text,
.sidebar-nav .option .icon {
  color: #FFFFFF;
}
.sidebar-nav .option .option-text {
  margin: 0;
}
.sidebar-nav .option .icon {
  justify-self: center;
  font-size: 2.3rem;
}
.sidebar-nav .option.theme {
  margin-bottom: 0;
}

.barra {
  background-color: #FFFFFF;
  padding: 0 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
}
.barra p {
  margin: 0;
  font-size: 1.7rem;
}
.barra p span {
  font-weight: bold;
  color: #4338CA;
}
.barra .logout {
  font-size: 2.5rem;
  color: #2563EB;
}

.panel .main {
  overflow-y: auto;
  padding: 2rem;
  padding-left: 4rem;
}
@media (min-width: 768px) {
  .panel .main {
    padding-left: 2rem;
  }
}
.panel .main .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}

.main .form .campo input {
  background-color: #FFFFFF;
}
.main .form .campo .icon {
  background-color: #FFFFFF;
}
.main .form input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  font-weight: bold;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .main .form input[type=submit] {
    width: auto;
  }
}
.main .form input[type=submit]:hover {
  background-color: #352ca3;
  cursor: pointer;
}

.enlace {
  margin: 2rem;
  display: inline-block;
}

.no-proyectos a {
  color: #4338CA;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-trim: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  background-color: #564ccf;
  min-height: 12rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  line-height: 2;
  transition: background-color 0.3s ease;
  border-radius: 1.5rem;
  text-transform: uppercase;
  font-size: 1.7rem;
  font-weight: bold;
  overflow: hidden;
  color: #FFFFFF;
}
.listado-proyectos .proyecto:hover {
  background-color: #3b31b8;
  cursor: pointer;
